import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule, OnInit } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule, Navbar } from 'ionic-angular';
import { IonicStorageModule } from '@ionic/storage';
import { FormsModule } from '@angular/forms';

import { MyApp } from './app.component';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
// import { GoogleMaps } from "@ionic-native/google-maps";
//import { formArrayNameProvider } from '@angular/forms/src/directives/reactive_directives/form_group_name';

import { AuthProvider } from '../providers/auth/auth';
import { UserStateProvider } from "../providers/userstate/user-state";
import { AlertsProvider } from '../providers/generic/AlertsProvider';
import { HttpClientModule, HTTP_INTERCEPTORS } from "@angular/common/http";
import { Geolocation } from '@ionic-native/geolocation'
import { autocompletePageModule } from '../pages/autocomplete/autocomplete.module';
import { LocationAccuracy } from '@ionic-native/location-accuracy';
import { LocationsProvider } from '../providers/Map/locations';
import { Diagnostic } from '@ionic-native/diagnostic';
// import { OpenNativeSettings } from '@ionic-native/open-native-settings';
// import { BackgroundGeolocation } from '@ionic-native/background-geolocation'
import { QRScanner } from '@ionic-native/qr-scanner';
// import { Sim } from '@ionic-native/sim';
import { UniqueDeviceID } from '@ionic-native/unique-device-id';
import { AppErrorHandler } from '../providers/generic/AppErrorHandler';
import { VehiclsProvider } from '../providers/Map/vechilsApi';
// import {  FacebookOriginal } from '@ionic-native/facebook';
import { Camera } from '@ionic-native/Camera';
import { File } from '@ionic-native/File/ngx';
import { WebView } from '@ionic-native/ionic-webview/ngx';
import { FilePath } from '@ionic-native/file-path/ngx';
import { FileTransfer, FileTransferObject } from '@ionic-native/file-transfer';
import { TokenInterceptor } from '../providers/http-error.interceptor';

@NgModule({
  declarations: [
    MyApp,
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpClientModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/before-scan/before-scan.module.ngfactory#BeforeScanPageModuleNgFactory', name: 'BeforeScanPage', segment: 'before-scan', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/endride/endride.module.ngfactory#EndridePageModuleNgFactory', name: 'EndridePage', segment: 'endride', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/freeride/freeride.module.ngfactory#FreeridePageModuleNgFactory', name: 'FreeridePage', segment: 'freeride', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/helprabbit/helprabbit.module.ngfactory#HelprabbitPageModuleNgFactory', name: 'HelprabbitPage', segment: 'helprabbit', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/history/history.module.ngfactory#HistoryPageModuleNgFactory', name: 'HistoryPage', segment: 'history', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/howtoride/howtoride.module.ngfactory#HowtoridePageModuleNgFactory', name: 'HowtoridePage', segment: 'howtoride', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/inridestatus/inridestatus.module.ngfactory#InridestatusPageModuleNgFactory', name: 'InridestatusPage', segment: 'inridestatus', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module.ngfactory#LoginPageModuleNgFactory', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/payment/payment.module.ngfactory#PaymentPageModuleNgFactory', name: 'PaymentPage', segment: 'payment', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/privatepolicy/privatepolicy.module.ngfactory#PrivatepolicyPageModuleNgFactory', name: 'PrivatepolicyPage', segment: 'privatepolicy', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/register/register.module.ngfactory#RegisterPageModuleNgFactory', name: 'RegisterPage', segment: 'register', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/scan-code/scan-code.module.ngfactory#ScanCodePageModuleNgFactory', name: 'ScanCodePage', segment: 'scan-code', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/selected-rabbit/selected-rabbit.module.ngfactory#SelectedRabbitPageModuleNgFactory', name: 'SelectedRabbitPage', segment: 'selected-rabbit', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/termsconditions/termsconditions.module.ngfactory#TermsconditionsPageModuleNgFactory', name: 'TermsconditionsPage', segment: 'termsconditions', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/upload-modal/upload-modal.module.ngfactory#UploadModalPageModuleNgFactory', name: 'UploadModalPage', segment: 'upload-modal', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/verfiypass/verfiypass.module.ngfactory#VerfiypassPageModuleNgFactory', name: 'VerfiypassPage', segment: 'verfiypass', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/maps/maps.module.ngfactory#MapsPageModuleNgFactory', name: 'MapsPage', segment: 'maps', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/mapsapi/mapsapi.module.ngfactory#MapsapiPageModuleNgFactory', name: 'MapsapiPage', segment: 'mapsapi', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/settingsrabbit/settingsrabbit.module.ngfactory#SettingsrabbitPageModuleNgFactory', name: 'SettingsrabbitPage', segment: 'settingsrabbit', priority: 'low', defaultHistory: [] }
  ]
}),
    autocompletePageModule,
    IonicStorageModule.forRoot({
      name: '__mydb',
      driverOrder: ['indexeddb', 'sqlite', 'websql', 'localStorage']    //,localStorage
     //driverOrder: ['websql','localStorage','indexeddb', 'sqlite']    //,localStorage
    })
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
  ],
  providers: [
    StatusBar,
    SplashScreen,
    //GoogleMaps,
    AuthProvider,
    VehiclsProvider,
    UserStateProvider,
    AlertsProvider,
    Geolocation,
    LocationAccuracy,  
    //FacebookOriginal,
    Diagnostic,
    //OpenNativeSettings,
    //BackgroundGeolocation,
    LocationsProvider,
    QRScanner,
    UserStateProvider,
    //Sim,
    Camera,
    File,
    WebView,
    FilePath,
    FileTransfer,
    FileTransferObject,
    UniqueDeviceID,
    Navbar,
    { provide: ErrorHandler, useClass: AppErrorHandler },//IonicErrorHandler
    { provide: HTTP_INTERCEPTORS, useClass: TokenInterceptor, multi: true },
  ]
})
export class AppModule { }



